.App {
  text-align: center;
}

nav {
  color: rgb(24, 32, 38);
}

.navbar{
  min-height: 10vh;
  max-height: 100px;
  margin: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.push {
  margin-left: auto;
}

button {
  margin: 7px;
  padding: 10px;
  width: 100px;
  align-items: center;
  background-color: white;
  /*background-image: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));*/
  border-radius: 3px;
  border-image-width: 1;
  border-width: 0;
  /*border-width: 1px;*/
  font-size: 20px;
  text-decoration: none;
  position: relative;
}

button:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: hotpink;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

button:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.App-middle {
  background-color: #282c34;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: hotpink;
}

